import React, {useState} from 'react';
import BackButton from '../common/BackButton';
import './Linker.scss';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import AutorenewIcon from '@material-ui/icons/Autorenew';
import styled from 'styled-components'
import { CopyToClipboard } from "react-copy-to-clipboard";
import SaveIcon from '@material-ui/icons/Save';
import DeleteIcon from '@material-ui/icons/Delete';
import translations from '../duplicator/data/translations.json'

const SpaceWrapper = styled.div`
    margin: 20px 0;
    text-align: center;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-content:center;
    align-items: stretch;
`

const Linker = () => {
    const { convert, copyText, reset } = translations.buttons;
    const { completeText, wrongLink } = translations.header;
    const [ link, setLink ] = useState('');
    const [ result, setResult ] = useState('');

    const convertLink = () => {
        if(!link){
            return alert(completeText)
        }
        
        if(link.indexOf(',p') < 0) {
            return alert(wrongLink)
        }
        const result = []
        let linksArray = link.split('\n');
        linksArray.map(link => {
            const findP = link.indexOf(',p');
            const lastMark = link.lastIndexOf(',');
            const getLetterP = link.substring(findP + 1, lastMark)
            result.push(getLetterP)
        } )
        setResult(result);
    };

    const resetLink = () => {
        setLink('')
        setResult('')
    };

    return (
        <div id="linker">
            <BackButton />
             <TextField
                id="outlined-multiline-static"
                label="Link"
                multiline
                rows={4}
                placeholder="Tutaj wklej link"
                variant="outlined"
                value={link}
                onChange={(e) => setLink(e.target.value)}
                disabled={result}
            />
              <SpaceWrapper>
                <Button variant="contained" onClick={convertLink} startIcon={<AutorenewIcon />}>
                    {convert}
                </Button>
            <CopyToClipboard text={result}>
                <Button variant="contained" color="primary" startIcon={<SaveIcon />}>
                    {copyText}
                </Button>
            </CopyToClipboard>
            <Button variant="contained" color="secondary" onClick={resetLink} startIcon={<DeleteIcon />}>
                {reset}
            </Button>
            </SpaceWrapper>
            <TextField
                id="outlined-multiline-static"
                label="Rezultat"
                multiline
                rows={4}
                variant="outlined"
                value={result}
            />
        </div>
    )
};

export default Linker